<template>
  <div class="container">
    <a-layout>
      <a-layout-header></a-layout-header>
      <a-layout-content>
        <div class="content">
          <h1 class="title">Wind Force赛事管理系统</h1>
          <a-tabs default-active-key="1" @change="callback">
            <a-tab-pane key="1" tab="账号登录">
              <a-form
                :form="form"
                :wrapper-col="{ span: 24 }"
                size="small"
                @submit="handleSubmit"
              >
                <a-form-item>
                  <a-input
                    ref="userNameInput"
                    placeholder="用户名"
                    v-decorator="[
                      'username',
                      {
                        rules: [
                          { required: true, message: 'User Name not empty!' },
                        ],
                      },
                    ]"
                  >
                    <a-icon slot="prefix" type="user" />
                  </a-input>
                </a-form-item>
                <a-form-item>
                  <a-input
                    ref="passwordInput"
                    placeholder="密码"
                    type="password"
                    v-decorator="[
                      'password',
                      {
                        rules: [
                          { required: true, message: 'Password not empty!' },
                        ],
                      },
                    ]"
                  >
                    <a-icon slot="prefix" type="unlock" />
                  </a-input>
                </a-form-item>
                <a-row class="auto-login" type="flex">
                  <a-col :span="12">
                    <a-checkbox v-model="isAutoLoginCheck">
                      自动登录
                    </a-checkbox>
                  </a-col>
                  <a-col :span="12" class="right">
                    <a-button type="link">
                      忘记密码？
                    </a-button>
                  </a-col>
                </a-row>

                <a-form-item :wrapper-col="{ span: 24 }">
                  <a-button
                    type="primary"
                    html-type="submit"
                    block
                    :loading="isLoginLoading"
                  >
                    登录
                  </a-button>
                </a-form-item>
              </a-form>
            </a-tab-pane>
            <a-tab-pane key="2" tab="用户注册" force-render>
              <p>注册页面还在开发中</p>
              <p>登录账号密码随便输入</p>
            </a-tab-pane>
          </a-tabs>
        </div>
      </a-layout-content>
      <a-layout-footer>
        Copyright © 2020 Wind Force街舞有限公司.
      </a-layout-footer>
    </a-layout>
  </div>
</template>

<script>
import storage from 'good-storage';
import { mapMutations } from 'vuex';
export default {
  data() {
    return {
      formLayout: 'horizontal',
      form: this.$form.createForm(this, { name: 'coordinated' }),
      isAutoLoginCheck: true,
      isLoginLoading: false,
    };
  },
  methods: {
    callback(key) {
      console.log(key);
    },
    handleSubmit(e) {
      e.preventDefault();
      this.form.validateFields((err, values) => {
        if (!err) {
          if (this.isAutoLoginCheck) {
            storage.set('windforceUsername', values.username);
            storage.set('windforcePassword', values.password);
          }
          this.isLoginLoading = true;
          this.request({
            url: '/admin/login',
            method: 'post',
            params: {
              username: values.username,
              password: values.password,
            },
          })
            .then(res => {
              if (res.data.status) {
                this.SET_USERTYPE(res.data.type);
                storage.set('windforceUserType', res.data.type);
              }
              this.isLoginLoading = false;
              this.$router.push({
                path: '/',
              });
            })
            .catch(() => {
              this.isLoginLoading = false;
            });
        }
      });
    },
    ...mapMutations(['SET_USERTYPE']),
  },
};
</script>

<style lang="less" scoped>
.ant-layout {
  height: 100vh;
  background-image: url(https://gw.alipayobjects.com/zos/rmsportal/TVYTbAXWheQpRcWDaDMu.svg);
  background-repeat: no-repeat;
  background-position: center 110px;
  background-size: 100%;
  .ant-layout-header {
    background: transparent;
  }
  .ant-layout-content {
    display: flex;
    justify-content: center;
    align-items: flex-start;
    .content {
      width: 300px;
      .title {
        color: @primary-color;
        font-size: 26px;
        margin: 10px 0;
      }
      .auto-login {
        .right {
          text-align: right;
          button {
            padding-right: 0;
          }
        }
      }
    }
  }
  .ant-layout-footer {
    text-align: center;
  }
}
</style>
